<template>
  <div id="app">
    <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
    <ResumeEditor ref="resumeEditor" :markdown="currentMarkdown" :enableHtml="enableHtml"></ResumeEditor>
  </div>
</template>

<script>
import StyleEditor from "./components/StyleEditor";
import ResumeEditor from "./components/ResumeEditor";
import "./assets/reset.css";

export default {
  name: "app",
  components: {
    StyleEditor,
    ResumeEditor
  },
  data() {
    return {
      // 动画延时
      interval: 3,
      currentStyle: "",
      enableHtml: false,
      fullStyle: [
        `/*
* Inspired by http://strml.net/
* 源码地址 https://gitee.com/zhousiwei/anires
* 大家好，我是杨乐。
* 我来写一份简历！
*/

/* 给所有元素加上过渡效果 */
* {
  transition: all .1s;
}
/* 设置背景颜色 */
html {
  color: rgb(222,222,222); background: rgb(0,43,54);
}

/* 设置边框 */
.styleEditor {
  padding: .5em;
  border: 1px solid;
  margin: .5em;
  overflow: auto;
  width: 45vw; height: 90vh;
  /* background: rgb(20,20,20); */
}
/* 代码高亮 */
.token.selector{ color: rgb(133,153,0); }
.token.property{ color: rgb(187,137,0); }
.token.punctuation{ color: yellow; }
.token.function{ color: rgb(42,161,152); }

/* 加3D效果 */
html{
  perspective: 1000px;
}
.styleEditor {
  position: fixed; left: 0; top: 0;
  -webkit-transition: none;
  transition: none;
  -webkit-transform: rotateY(10deg) translateZ(-100px) ;
          transform: rotateY(10deg) translateZ(-100px) ;
}

/* 准备一个编辑器 */
.resumeEditor{
  position: fixed; right: 0; top: 0;
  padding: .5em;  margin: .5em;
  width: 48vw; height: 90vh;
  border: 1px solid;
  background: white; color: #222;
  overflow: auto;
}
/* 开始写简历 */
`,
        `
/*将Markdown格式翻译成HTML
 *再对HTML加点样式
*/
.resumeEditor{
  padding: 2em;
}
.resumeEditor h2{
  display: inline-block;
  border-bottom: 1px solid;
  margin: 1em 0 .5em;
}
.resumeEditor ul,.resumeEditor ol{
  list-style: none;
}
.resumeEditor ul> li::before{
  content: '•';
  margin-right: .5em;
}
.resumeEditor ol {
  counter-reset: section;
}
.resumeEditor ol li::before {
  counter-increment: section;
  content: counters(section, ".") " ";
  margin-right: .5em;
}
.resumeEditor blockquote {
  margin: 1em;
  padding: .5em;
  background: #ddd;
}
`
      ],
      currentMarkdown: "",
      fullMarkdown: `
杨乐
----
27岁，前端工程师，四年从业经验，目前在上海工作。
有良好的文档编写和代码书写规范，能独立解决问题、百折不挠
<br/>

- [在线简历](https://ylnlh.github.io/cv/)

技能
----
* 前端开发
* 单点登录
* 权限管理
* 公众号/小程序开发
* 小程序开发
* uniapp混合开发

技术及语言
----
  - **前端**: VueJs、Bootstrap、UniApp、jQuery、公众号、小程序、mPass、TS
  - **Others**: Git、Svn、XMind
  - 熟悉前端工程化开发，并有实践经验（如 webpack 等）
  - 熟悉 jquery、Bootstrap 等前端技术，可以使用 rem 结合响应式布局，flex 布局，百分 比布 局兼容移动端适配，能够结合 zepto、mui 进行移动端的 web 开发
  - 熟练掌握前端相关技术 HTML/CSS/JavaScript，了解相关开发调试工具
  - 熟练使用原生 JS 进行 BOM 和 DOM 操作，使用第三方类库实现页面动态效果
  - 熟练使用代码管理工具 Git，svn，同时熟悉包依赖工具 npm 的使用，简单了解 Node.js、 express 框架的使用；
  - 熟练使用小程序开发，并有完整项目开发经验
  - 熟练掌握 Uniapp 框架，结合 HBuilderX 编辑器，开发 app，小程序等跨平台应用
  - 项目种类丰富，包含pc（管理系统/数据中台/官网）、移动端h5（公众号/App混合开发）、小程序，熟悉百度、高德地图，echarts

链接
----
* [技术博客](https://blog.csdn.net/yanglenlh/)

项目
----
1. [基于vue3 + vuex + vue-router + echars5 + + element-plus高德地图的管理框架]
2. [基于vue2 + Vuex + element-ui +高德地图的crm后台管理系统]
3. [基于uniapp + mpass的嵌入app小程序]
4. [微信小程序 新橙社Lite 等]
5. [vue3 + 企业微信SDK + vant3的企微H5]
6. [vue2 + 微信SDK的h5]
7. [uniapp + iview开发的app]


联系我
----
* 联系QQ：**1933026317** | 微信：**15903753983**
* 主要涉及技术：**前端开发**、**公众号开发**、**小程序**、**UniApp**
`
    };
  },
  created() {
    this.makeResume();
  },

  methods: {
    makeResume: async function() {
      await this.progressivelyShowStyle(0);
      await this.progressivelyShowResume();
      await this.progressivelyShowStyle(1);
      await this.showHtml();
      await this.progressivelyShowStyle(2);
    },
    showHtml() {
      return new Promise((resolve, reject) => {
        this.enableHtml = true;
        resolve();
      });
    },
    progressivelyShowStyle(n) {
      return new Promise((resolve, reject) => {
        let interval = this.interval;
        let showStyle = async function() {
          let style = this.fullStyle[n];
          if (!style) {
            return;
          }
          // 计算前 n 个 style 的字符总数
          let length = this.fullStyle
            .filter((_, index) => index <= n)
            .map(item => item.length)
            .reduce((p, c) => p + c, 0);
          let prefixLength = length - style.length;
          if (this.currentStyle.length < length) {
            let l = this.currentStyle.length - prefixLength;
            let char = style.substring(l, l + 1) || " ";
            this.currentStyle += char;
            if (style.substring(l - 1, l) === "\n" && this.$refs.styleEditor) {
              this.$nextTick(() => {
                this.$refs.styleEditor.goBottom();
              });
            }
            setTimeout(showStyle, interval);
          } else {
            resolve();
          }
        }.bind(this);
        showStyle();
      });
    },
    progressivelyShowResume() {
      return new Promise((resolve, reject) => {
        let length = this.fullMarkdown.length;
        let interval = this.interval;
        let showResume = () => {
          if (this.currentMarkdown.length < length) {
            this.currentMarkdown = this.fullMarkdown.substring(
              0,
              this.currentMarkdown.length + 1
            );
            let lastChar = this.currentMarkdown[
              this.currentMarkdown.length - 1
            ];
            let prevChar = this.currentMarkdown[
              this.currentMarkdown.length - 2
            ];
            if (prevChar === "\n" && this.$refs.resumeEditor) {
              this.$nextTick(() => this.$refs.resumeEditor.goBottom());
            }
            setTimeout(showResume, interval);
          } else {
            resolve();
          }
        };
        showResume();
      });
    }
  }
};
</script>

<style scoped>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  min-height: 100vh;
}

* {
  box-sizing: border-box;
}
</style>



